<template>
  <div>
      <div class="first-section">
        <div class="function-type">
            <button @click ="functionTypeSigmoid = !functionTypeSigmoid"> Keisti funkcijos tipą
            </button>
            {{functionTypeSigmoid ? 'Sigmoidinė' : 'Slenkstinė'}}
            <img :src="getFunctionImagePath">
        </div>
        <div class="dataset-type">
          <button class="change-dataset" @click ="(dataSet = (dataSet =='Setosa' ? 'Versicolor' : 'Setosa')); showResult =false; showTestResult = false"> Keisti duomenų rinkinį</button>
          <div class="dataset-info">
            {{ (dataSet == 'Setosa') ? 'Setosa - 50 įrašų (klasė: 0),\n\n Versicolor ir Virginica - 100 įrašų (klasė: 1)' : 'Versicolor - 50 įrašų (klasė: 0), Virginica - 50 įrašų (klasė: 1)'}}
          </div>
        </div>
        <div class="variables">
          <div class="learn-rate">
            Mokymosi greitis (learning rate):
            {{learn_rate}}
            <input type="range" step="0.1" min="0.1" max="5" v-model="learn_rate">
          </div>
          <div class="epoch-count">
            Epochų skaičius:
            <input type="number" min="1" max="3000" v-model="epochCount">
          </div>
        </div>
        <div class="output-buttons">
          <button class="train" @click ="training();showResult=true">
            Treniruoti
          </button>
          <button v-if="showResult" class="test" @click="testing();showTestResult=true">
            Testuoti
          </button>
        </div>
        <div v-if="showResult" class="output">
          <div class="training-result">
            <div>
              Klasifikavimo tikslumas:
              {{ classificationAccuracy.toFixed(2) }}
              Paklaida:
              {{ error }}
            </div>
            <div>
              Gauti svoriai:
              <div>
                Slenkstis w0 (bias): {{bias.toFixed(3)}}
              </div>
              <div v-for="(weight, index) in weights" :key="index">
                w{{index+1}}: {{weight.toFixed(3)}}
              </div>
            </div>
          </div>
          <div class="testing-result--container">
             <div v-if="showTestResult" class="testing-result">
              <div>
                Klasifikavimo tikslumas: {{(total /(xTestInput[0].length+xTestInput[1].length)).toFixed(2)}}
              </div>
              {{dataSet == "Setosa" ? 'Setosa' : 'Versicolor'}} (klasė 0): {{classifiedCorrectly.class0}} / {{ xTestInput[0].length}} 
              <br/>
              <div>
                Paklaida (klasė: 0): {{this.classifiedCorrectly['error0']}} 
              </div>
              {{dataSet == "Setosa" ? 'Versicolor/Virginica' : 'Virginica'}} (klasė 1): {{classifiedCorrectly.class1}} / {{ xTestInput[1].length}}
              <div>
                Paklaida (klasė: 1): {{this.classifiedCorrectly['error1']}} 
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import dataFile from '@/assets/dataset/iris1.txt'
import dataFile2 from '@/assets/dataset/iris2.txt'
export default {
    name: 'PerceptronTraining',
    data(){
      return{
        fullDataset: [],
        xInput: [],
        xTestInput: [[],[]],
        weights: [],
        actualClassIndex: 0,
        dataSet: 'Setosa',
        learn_rate: 0.1,
        error: 1,
        bias: 0,
        classificationAccuracy: 0,
        functionTypeSigmoid: false,
        epochCount: 1,
        sum: 0,
        showResult: false,
        showTestResult: false,
        classifiedCorrectly: {
          'class0' : 0,
          'class1' : 0,
          'error0': 0,
          'error1': 0,
          'total': 0,
        }
  
      }
    },
    mounted(){

    },
     computed:{
        getFunctionImagePath(){
            if(this.functionTypeSigmoid) return require(`../assets/sigmoid.png`);
            else return require(`../assets/threshold.png`);

        }
    },
    methods:{
      testing(){
        this.classifiedCorrectly['class0'] = 0;
        this.classifiedCorrectly['class1'] = 0;
        this.classifiedCorrectly['error0'] = 0;
        this.classifiedCorrectly['error1'] = 0;
        var prediction = 0;
         for(var i = 0; i < this.xTestInput[0].length; i++){
          prediction = this.predict(this.xTestInput[0][i]);
          if(parseFloat(this.xTestInput[0][i][this.actualClassIndex]) === prediction[0]) this.classifiedCorrectly['class0'] +=1;
          this.classifiedCorrectly['error0'] += Math.abs(prediction[1]);
         }
         for(var j = 0; j < this.xTestInput[1].length; j++){
          prediction = this.predict(this.xTestInput[1][j]);
          if(parseFloat(this.xTestInput[1][j][this.actualClassIndex]) === prediction[0]) this.classifiedCorrectly['class1']+=1;
          this.classifiedCorrectly['error1'] += Math.abs(prediction[1]);
         }
         this.total = this.classifiedCorrectly['class0'] + this.classifiedCorrectly['class1'];
         if(this.functionTypeSigmoid){
            this.classifiedCorrectly['error0'] = this.classifiedCorrectly['error0'].toFixed(2);
            this.classifiedCorrectly['error1'] = this.classifiedCorrectly['error1'].toFixed(2);
         }

      },
      training(){
        var epoch = 0;
        var accuracy = 0
        var sampleCount = 0;
        this.error = 1;
        this.convertSelectedDataset();
        this.generateWeights();
        var inputLength = this.xInput.length;
        while(this.error != 0 && epoch <this.epochCount){
          this.error = 0;
          accuracy = 0;
          sampleCount = 0;
          for(var i = 0; i< inputLength; i++){
            var prediction = this.predict(this.xInput[i]);
            if(parseFloat(this.xInput[i][this.actualClassIndex]) === prediction[0]) accuracy +=1;
            else{
              var loss = prediction[1];
              this.error += Math.abs(loss);
              for(var w =0; w <= this.weights[0].length; w++){
                this.weights[w] += loss*parseFloat(this.xInput[i][w]) *this.learn_rate;
              }
              
              this.bias += loss * this.learn_rate;
            }
            sampleCount +=1;
            this.classificationAccuracy = accuracy/sampleCount;
          }
          this.error = this.error.toFixed(2);
          epoch++;
        }
      },
      activation(){
        if(this.functionTypeSigmoid){
          return (1/(1+Math.pow(Math.E, (-this.sum)))) <= 0.5 ? 0 : 1;
        }
        else return this.sum <=0 ? 0 : 1;
      },
      predict(input){
        this.sum = this.bias;
        this.weights.forEach((w, index) => {
              this.sum += parseFloat(input[index]) * w;
            });
        var calcError = 0;
        if(this.functionTypeSigmoid) calcError =  this.calculateError(input[4], (1/(1+Math.pow(Math.E, (-this.sum)))));
        else calcError =  this.calculateError(input[4], (this.sum <=0 ? 0 : 1));
        calcError = calcError.toFixed(2);
        return [this.activation(), parseFloat(calcError)];
      },
      generateWeights(){
        this.weights = Array(this.actualClassIndex);
        for(var j = 0; j< this.weights.length; j++){
          this.weights[j] = Math.random();
        }
        this.bias = Math.random();
      },
      convertSelectedDataset(){
        this.xInput = [];
        this.xTestInput = [[],[]];
        var irisType = '';
        var splitData;
        if(this.dataSet == 'Setosa') {
          irisType = 'Iris-setosa';
          splitData = dataFile.split("\n");
        }
        else if(this.dataSet == 'Versicolor') {
          irisType ='Iris-versicolor';
          splitData = dataFile2.split("\n");
        }
      
        var countTesting = splitData.length*0.1;
        var it = 0, it1=0, it2=0;
        for(var i = 0; i < splitData.length; i++){
              this.fullDataset[i] = this.classificateDataset([...splitData[i].split(",")], irisType);
              if(this.fullDataset[i][4] == 0 && this.xTestInput[0].length <countTesting){
                this.xTestInput[0][it1] = this.fullDataset[i];
                it1++;
              }
              else if(this.fullDataset[i][4] == 1 && this.xTestInput[1].length <countTesting){
                this.xTestInput[1][it2] = this.fullDataset[i];
                it2++;
              }
              else {
                this.xInput[it] = this.fullDataset[i];
                it++;
              }      
        }
        this.actualClassIndex = this.xInput[0].length-1;
      },
      classificateDataset(input, irisType){
        if(this.dataSet != "Setosa")
          input[4] = input[4].slice(0, -1);
        input[4] = (input[4] == irisType) ? 0 : 1;
        return input;
      },
      calculateError(expected, predicted){
        return parseFloat(expected - predicted);
      }
    },
}
</script>

<style lang="scss">
  .dataset-type{
    display: flex;
    flex-direction: column;
    margin: 15px 0;
    border: 3px solid #eaeaea;
    padding: 10px 0;
    border-radius: 14px;
    .change-dataset{
      flex: 1;
      margin-right: 17px;
      height: 30px;
      align-self: center;
    }
    .dataset-info{
      flex: 1;
      text-align: center;
      margin-top: 15px;
    }
  }
  .output-buttons{
    display: flex;
    justify-content: space-around;
    margin: 10px auto;
  }
  .variables{
     border: 3px solid #eaeaea;
    padding: 10px 0;
    border-radius: 14px;
  }
  .output{
    display: flex;
    flex-direction: row;
    margin: 15px 0;
    .training-result{
      flex: 1;
    }
    .testing-result--container{
      flex: 1;
      .testing-result{
        display: flex;
        justify-content: center;
        flex-direction: column;
      }
    }
  }
</style>